<template>
  <div class="about">
    <input  type="Button" value="Почетна страница" v-on:click="redirect('home')"/><br/><br/><br/>
    <h1>Регистација</h1>
    Корисничко име: <br/>
    <input type="text" v-model="userName"/><br/>
    Лозинка: <br/>
    <input type="password" v-model="pass"/><br/><br/>
    <input type="button" value="Региструј ме" v-on:click="register()"/><br/><br/>
    
    <p v-on:click="redirect('login')" class="link">Можете такође да се пријавите кликом на ово  </p> 
    
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      pass:'',
      userName:''
    }
  },
  mounted(){
    let korisnik=document.cookie.match("token")
    if(korisnik){
      this.$router.push({name:"home"})
    }
  },
  methods:{
    register(){
      let podaci={korisnickoIme: this.userName,lozinka: this.pass}
      fetch('http://localhost/admin/korisnik/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
        body: JSON.stringify(podaci)
    })
    .then((resp)=>{
      if(resp.status!=400 && resp.status!=500){
        alert("Успешно сте се регистровали");
        this.$router.push({name:'home'})
      }
      })
    .catch( err => alert("Неуспрешна регистрација"+err));

    },
    redirect(argument){
      this.$router.push({name:argument})
    }
  }
}
</script>


<style>
.link{
  color:blue;
}
</style>